<template lang="html">
  <!--  -->
  <section class="full-fixed order-box">
    <ul class="nav">
      <li class="list" :class="{'active': currentIndex===0}" @click="_openCut('all', 0)"></li>
      <li class="list" :class="{'active': currentIndex===1}" @click="_openCut('deliver', 1)"></li>
      <li class="list" :class="{'active': currentIndex===2}" @click="_openCut('take', 2)"></li>
      <li class="list" :class="{'active': currentIndex===3}" @click="_openCut('remain', 3)"></li>
      <li class="list" :class="{'active': currentIndex===4}" @click="_openCut('after', 4)"></li>
    </ul>
    <div class="main">
      <scroll
        ref='scroll'
        :pullDownRefresh="pullDownRefreshObj"
        :pullUpLoad="pullUpLoadObj"
        @pullingDown="onPullingDown"
        @pullingUp="onPullingUp"
      >
        <div>
          <router-view name="main"></router-view>
        </div>
      </scroll>
    </div>
    <!--  -->
    <div class="else">
      <router-view name="else"></router-view>
    </div>
  </section>
</template>

<script>
  /**
  * 
  *  state
  * state: 0 
  * state: 1 
  * state: 2 
  * state: 3 
  **/
  import Scroll from '@/components/vertical-Scroll'
  import {mapGetters, mapMutations} from 'vuex'

  export default {
    beforeRouteEnter (to, from, next) {   //  true
      next(vm => {
        vm._cutActive()
        vm.$emit('destroy', true)
      })
    },
    beforeRouteLeave(to, from, next) {  //  false
      if (to.name === 'submitorder') {    // 
        this.setRouterAnim(false)
      }
      this.$emit('destroy', false)
      next()
    },
    data() {
      return {
        currentIndex: 0,
        pullDownRefreshObj: {
          threshold: 50,
          stop: 50
        },
        pullUpLoadObj: {
          threshold: 50
        },
      }
    },
    created() {
      this._cutActive()
    },
    computed: {
      transition() {
        return this.routerAnim ? 'transXL' : ''
      },
      ...mapGetters([
        'routerAnim'
      ])
    },
    methods: {
      // 
      onPullingDown() {
        this.$refs.scroll.forceUpdate();
      },
      // 
      onPullingUp() {
        this.$refs.scroll.forceUpdate();
      },
      _cutActive() {    // 
        const pathName = this.$route.name
        switch (pathName) {
          case 'all':
            this.currentIndex = 0
            break
          case 'deliver':
            this.currentIndex = 1
            break
          case 'take':
            this.currentIndex = 2
            break
          case 'remain':
            this.currentIndex = 3
            break
          case 'after':
            this.currentIndex = 4
            break
          default:
            this.currentIndex = 0
        }
      },
      _openCut(cut, index) {    // 
        this.setRouterAnim(false)
        this.currentIndex = index
        this.$router.replace({
          path: `/mycenter/orderbox/${cut}`
        });
        this.$refs.scroll.scrollTo(0, 0, 30);
      },
      ...mapMutations({
        'setRouterAnim': 'SET_ROUTER_ANIM'
      })
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/sass/variable";
  @import "../../common/sass/mixin";

  .order-box {
    overflow: hidden;
    background-color: $color-background-e;
    .nav {
      @include border-b-1px(0);
      position: relative;
      z-index: 8;
      display: flex;
      align-items: center;
      height: 1.2rem /* 45/37.5 */;
      background-color: #fff;
      .list {
        flex: 1;
        font-size: .43rem /* 16/37.5 */;
        line-height: 1.2rem /* 45/37.5 */;
        text-align: center;
        &.active {
          color: $color-theme;
        }
      }
    }
    .main {
      position: absolute;
      top: 1.2rem /* 45/37.5 */;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
</style>



